import { Module } from 'src/app/sdk/user/models/module';
import { Component, OnInit } from '@angular/core';
import { UserModuleService } from 'src/app/sdk/user/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService, NzFormatEmitEvent, NzTreeNode, NzDropdownMenuComponent, NzContextMenuService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private userModuleService: UserModuleService,
        private spinner: NgxSpinnerService,
        private nzMessageService: NzMessageService,
        private nzContextMenuService: NzContextMenuService,
        private router: Router,
        private pesan: PesanService,

    ) { }



    listOfData = []
    search: string = ''

    ngOnInit() {
        this.searchData()
    }
    nzEvent(event: NzFormatEmitEvent): void {
        console.log(event);
    }

    searchData(reset: boolean = false): void {
        this.spinner.show()
        this.userModuleService
            .getUserModules()
            .subscribe((datas: any) => {

                function unflatten(arr) {
                    var tree = [],
                        mappedArr = {},
                        arrElem,
                        mappedElem;

                    // First map the nodes of the array to an object -> create a hash table.
                    for (var i = 0, len = arr.length; i < len; i++) {
                        arrElem = arr[i];
                        mappedArr[arrElem.key] = arrElem;
                        // mappedArr[arrElem.key]['children'] = [];
                    }


                    for (var key in mappedArr) {

                        if (mappedArr.hasOwnProperty(key)) {
                            mappedElem = mappedArr[key];

                            // If the element is not at the root level, add it to its parent array of children.
                            if (mappedElem.parent) {

                                if (!mappedArr[mappedElem['parent']].hasOwnProperty('children')) {
                                    mappedArr[mappedElem['parent']]['children'] = []
                                    mappedArr[mappedElem['parent']]['isLeaf'] = false
                                }

                                if (!mappedElem.hasOwnProperty('children')) {
                                    mappedElem['isLeaf'] = true
                                }
                                mappedArr[mappedElem['parent']]['children'].push(mappedElem);
                            }
                            // If the element is at the root level, add it to first level elements array.
                            else {
                                tree.push(mappedElem);
                            }
                        }
                    }
                    return tree;
                }

                let listData = datas.data;
                listData.forEach((item, index) => {
                    listData[index] = {
                        title: item.name_module,
                        key: item.id_module,
                        parent: item.id_module_parent,
                        // children: [],
                        expanded: true,
                    }
                });

                this.listOfData = unflatten(listData);
                console.log(this.listOfData)
                this.spinner.hide()

            })
    }



    id: string = '';
    isLeaf: boolean = false;
    delete() {
        if (!this.isLeaf) {
            this.pesan.pesanWarningForm('tidak bisa di hapus, harus anak paling kecil!')
            return false;
        }
        this.spinner.show()
        this.userModuleService.deleteUserModulesId(this.id).subscribe(
            data => {
                this.searchData()
                this.spinner.hide()
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }


    openFolder(data: NzTreeNode | Required<NzFormatEmitEvent>): void {
        // do something if u want
        if (data instanceof NzTreeNode) {
            data.isExpanded = !data.isExpanded;
        } else {
            const node = data.node;
            if (node) {
                node.isExpanded = !node.isExpanded;
            }
        }
    }

    activedNode: NzTreeNode;
    activeNode(data: NzFormatEmitEvent): void {
        this.activedNode = data.node!;
    }

    contextMenu($event: MouseEvent, key: string, isLeaf: boolean, menu: NzDropdownMenuComponent): void {
        this.id = key;
        this.isLeaf = isLeaf;
        this.nzContextMenuService.create($event, menu);
    }

    addChild(): void {
        // do something
        this.router.navigate(['/users/module/add', this.id])
    }

    edit(): void {
        // do something
        this.router.navigate(['/users/module/edit', this.id])
    }




}
