import { Component, OnInit } from '@angular/core';
import { UserModuleService, UserModuleAppService } from 'src/app/sdk/user/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Module_app, Module } from 'src/app/sdk/user/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(

        private userModuleService: UserModuleService,
        private userModuleAppService: UserModuleAppService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,

    ) { }

    id_module: string = ''
    dataModule: Module = {}
    listModule: Module_app [] = []

    ngOnInit() {
        this.id_module = this.activatedRoute.snapshot.paramMap.get('id_module')
        this.form()
        this.getPreeData()
        this.getData()
    }

    getPreeData() {
        this.userModuleAppService.getUserModuleApps().subscribe(
            data=> {
                this.listModule = data.data
            }
        )
    }

    getData() {
        this.userModuleService.getUserModulesId(this.id_module).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Module>
    form() {
        this.dataForm = this.fb.group<Module>({
            name_module: new FormControl('', [Validators.required]),
            id_module_parent: new FormControl(''),
            order_by: new FormControl(''),
            id_module: new FormControl(''),
            key: new FormControl(''),
            icon: new FormControl(''),
            url: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataModule = this.dataForm.value
        this.userModuleService
            .putUserModulesId({id: this.id_module, body: this.dataModule})
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }



    goToBack() {

        this.router.navigate(['users/module/list'])
    }

}
