import { Module } from 'src/app/sdk/user/models/module';
import { UserModuleAppService, UserModuleService } from 'src/app/sdk/user/services';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Promosi } from 'src/app/sdk/hr/models/promosi';
import { Pegawai } from 'src/app/sdk/hr/models/pegawai';
import { Module_app } from 'src/app/sdk/user/models';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private userModuleAppService: UserModuleAppService,
        private userModuleService: UserModuleService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }



    dataModule: Module = {}
    dataParent: Module_app = {}


    id_module: string = ''

    ngOnInit() {
        this.id_module = this.routerActive.snapshot.paramMap.get('id_module')
        this.getModule();
    }
    getModule() {
        this.spinner.show()
        this.userModuleService.getUserModulesId(this.id_module).subscribe(
            response => {
                this.dataModule = response.data
                this.getParent(response.data.id_module_parent)
            }
        )

    }
    getParent(id_module_parent) {
        this.userModuleAppService.getUserModuleAppsId(id_module_parent).subscribe(
            response => {
                this.dataParent = response.data
                this.spinner.hide()
            }
        )
    }


    goToBack() {

        this.router.navigate(['/users/module/list'])
    }



}
