import { Module } from 'src/app/sdk/user/models/module';
import { Component, OnInit } from '@angular/core';
import { UserModuleService, UserModuleAppService } from 'src/app/sdk/user/services';
import { Module_app } from 'src/app/sdk/user/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(

        private userModuleService: UserModuleService,
        private userModuleAppService: UserModuleAppService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activeRouter: ActivatedRoute

    ) { }

    dataModule: Module = {}
    listModule: Module_app[] = []

    id_module_parent: string = ''
    ngOnInit() {

        this.activeRouter.paramMap.subscribe(
            data => {
                if (data.get('id_module_parent')) {
                    if (data.get('id_module_parent') != 'parent') {
                        this.id_module_parent = data.get('id_module_parent');
                    }
                    else {
                        this.getPreeData()
                    }
                }
            }
        )

        this.form()

    }

    getPreeData() {
        this.userModuleAppService.getUserModuleApps().subscribe(
            data => {
                this.listModule = data.data
            }
        )
    }

    dataForm: FormGroupTypeSafe<Module>
    form() {
        this.dataForm = this.fb.group<Module>({
            name_module: new FormControl('', [Validators.required]),
            id_module_parent: new FormControl(this.id_module_parent),
            order_by: new FormControl(''),
            key: new FormControl(''),
            url: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataModule = this.dataForm.value
        this.userModuleService
            .postUserModules(this.dataModule)
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }



    goToBack() {

        this.router.navigate(['users/module/list'])
    }

}
